@import "src/common/styles/sass/variables.scss";


.footerBlock {

  .footerContainer {
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    text-align: center;
  }


  .iconBlock {
    max-width: 400px;
    width: 90%;
    display: flex;
    margin: 50px;
    border: 1px solid;
    justify-content: space-between;


    .iconItem {
      background-color: lightgray;
      height: 60px;
      width: 60px;
    }
  }

  .title {
    color: $backgroundColorHeader;
  }
}






